.services {
	padding : 24px 0;

	@media screen and (min-width : $break-table) {
		padding : 32px 0;
	}

	@media screen and (min-width : $break-desktop) {
		padding : 40px 0;
	}
}

.services__header {
	margin-bottom : 24px;

	@media screen and (min-width : $break-table) {
		margin-bottom : 32px;
	}

	@media screen and (min-width : $break-desktop) {
		margin-bottom : 48px;
	}

	.swiper-nav {
		@media screen and (min-width : $break-table) {
			display : none;
		}
	}
}

.swiper--services {

	.swiper-wrapper {
		@media screen and (min-width : $break-table) {
			box-sizing            : border-box;
			display               : grid;
			grid-column-gap       : 6px;
			grid-template-columns : repeat(5, 1fr);
			margin                : 0 auto;
			max-width             : 679px;
		}

		@media screen and (min-width : $break-desktop) {
			grid-row-gap          : 6px;
			grid-template-columns : repeat(6, 1fr);
			margin                : 0;
			max-width             : none;
		}
	}

	.swiper-slide {
		&:nth-of-type(1) {

			@media screen and (min-width : $break-table) {
				grid-column : 1/3;
				grid-row    : 1/3;
			}

			@media screen and (min-width : $break-desktop) {
				grid-column : unset;
				grid-row    : 1/5;
			}
		}

		&:nth-of-type(2) {

			@media screen and (min-width : $break-table) {
				grid-column : 3/5;
				grid-row    : 1/3;
			}

			@media screen and (min-width : $break-desktop) {
				grid-column : unset;
				grid-row    : 3/7;
				margin-left : -66px;
			}
		}


		&:nth-of-type(3) {

			@media screen and (min-width : $break-table) {
				grid-column : 2/4;
				grid-row    : 3/5;
				margin-top  : -74px;
			}

			@media screen and (min-width : $break-desktop) {
				grid-column : unset;
				grid-row    : 5/10;
				margin-top  : 0;
			}

			.services__item {
				background-color : $blue;
			}

			.services__text {
				color : $white;
			}
		}

		&:nth-of-type(4) {

			@media screen and (min-width : $break-table) {
				grid-column : 4/7;
				grid-row    : 3/5;
				margin-top  : -74px;
			}

			@media screen and (min-width : $break-desktop) {
				grid-column : unset;
				grid-row    : 1/5;
				margin-left : -66px;
				margin-top  : 0;
			}

			.services__item {
				background-color : $blue;
			}

			.services__text {
				color : $white;
			}

		}

		&:nth-of-type(5) {


			@media screen and (min-width : $break-table) {
				grid-column : 1/3;
				grid-row    : 5/7;
				margin-top  : -74px;
			}

			@media screen and (min-width : $break-desktop) {
				grid-column : unset;
				grid-row    : 5/10;
				margin-left : -66px;
				margin-top  : 0;
			}
		}

		&:nth-of-type(6) {


			@media screen and (min-width : $break-table) {
				grid-column : 3/5;
				grid-row    : 5/7;
				margin-top  : -74px;
			}

			@media screen and (min-width : $break-desktop) {
				grid-column : unset;
				grid-row    : 3/7;
				margin-left : -66px;
				margin-top  : 0;
			}
		}

		&:nth-of-type(7) {


			@media screen and (min-width : $break-table) {
				grid-column : 2/4;
				grid-row    : 7/9;
				margin-top  : -74px;
			}

			@media screen and (min-width : $break-desktop) {
				grid-column : unset;
				grid-row    : 1/5;
				margin-left : -66px;
				margin-top  : 0;
			}
		}

		&:nth-of-type(8) {

			@media screen and (min-width : $break-table) {
				grid-column : 4/7;
				grid-row    : 7/9;
				margin-top  : -74px;
			}

			@media screen and (min-width : $break-desktop) {
				grid-column : unset;
				grid-row    : 5/10;
				margin-left : -66px;
				margin-top  : 0;
			}

			.services__item {
				background-color : $blue;
			}

			.services__text {
				color : $white;
			}
		}
	}
}

.services__grid {
	display : grid;


	@media screen and (min-width : $break-table) {
		grid-column-gap       : 6px;
		grid-template-columns : repeat(5, 1fr);
		margin                : 0 auto;
		max-width             : 679px;
	}

	@media screen and (min-width : $break-desktop) {
		grid-row-gap          : 6px;
		grid-template-columns : repeat(6, 1fr);
		margin                : 0;
		max-width             : none;
	}
}

.services__item {
	align-items         : center;
	background-color    : #F1F5F6;
	display             : flex;
	flex-direction      : column;
	justify-content     : center;
	mask-image          : url("../images/home/service/services-table.svg");
	mask-position       : 50% 50%;
	-webkit-mask-repeat : no-repeat;
	-webkit-mask-size   : contain;
	min-height          : 325px;
	min-width           : 267px;
	text-align          : center;

	@media screen and (min-width : $break-table) {
		-webkit-mask-size : cover;
	}

	@media screen and (min-width : $break-desktop) {
		mask-image : url("../images/home/service/services.svg");
		min-height : 235px;
		min-width  : 279px;
	}

}

.services__icon {
	margin-bottom : 12px;
	text-align    : center;
}

.services__text {
	color       : #232323;
	font-size   : 16px;
	font-weight : 500;
	line-height : 24px;
	margin      : 0 auto;
	max-width   : 189px;
}
