.header {
	background-color : $white;
	padding          : 13px 0;
}

.header__container {
	max-width : 1372px;
	padding   : 0 16px;
}

.header--data {

}

.header__logo {
	height : 29px;
	width  : 100px;

	@media screen and (min-width : $break-table) {

	}

	@media screen and (min-width : $break-desktop) {
		height : 36px;
		width  : 124px;
	}
}

.header__wrapper {
	align-items     : center;
	display         : flex;
	justify-content : space-between;
}

.nav {
	display : none;

	@media screen and (min-width : $break-table) {
		display : block;
	}

	&--marketing {
		display : none;
		@media screen and (min-width : 1330px) {
			display : block;
		}
	}
}

.nav__list {
	align-items : center;
	display     : flex;
}

.nav__item {
	& + & {

		@media screen and (min-width : $break-table) {
			margin-left : 40px;
		}

		@media screen and (min-width : $break-desktop) {
			margin-left : 48px;
		}
	}
}

.nav__link {
	font-size   : 16px;
	font-weight : 700;
	line-height : 22px;
	position    : relative;


	&:before {
		bottom     : -5px;
		content    : "";
		height     : 2px;
		left       : 0;
		position   : absolute;
		transition : width .3s ease;
		width      : 0;
		z-index    : 1;
	}

	&:hover {

		&:before {
			width : 100%;
		}
	}

	&--main {
		color : #232323;

		&:before {
			background-color : $blue;
		}
	}

	&--data {
		&:before {
			background-color : $dark-blue;
		}
	}

	&--marketing {
		&:before {
			background-color : $orange;
		}
	}
}

.nav--data {

}

.header__home {
	background-color : transparent;
	border           : none;

	span {
		display : none;
	}

	svg {
		stroke : $blue;

		path {
			fill : $blue;
		}
	}

	@media screen and (min-width : $break-table) {

	}

	@media screen and (min-width : $break-desktop) {
		background-color : $blue;
		border           : 1px solid $blue;
		svg {
			display : none;
		}

		span {
			display : block;
		}
	}
}

.header__data {
	background-color : transparent;
	border           : none;

	span {
		display : none;
	}

	svg {
		path {
			fill : $dark-blue;
		}

		rect {
			stroke : $dark-blue;
		}
	}

	@media screen and (min-width : $break-table) {

	}

	@media screen and (min-width : $break-desktop) {
		background-color : $dark-blue;
		border           : 1px solid $dark-blue;

		&:hover {

			span {
				color : $dark-blue;
			}
		}

		svg {
			display : none;
		}

		span {
			background-color : $dark-blue;
			display          : block;
		}
	}
}

.header__marketing {
	background-color : transparent;
	border           : none;

	span {
		display : none;
	}

	svg {
		path {
			fill : $orange;
		}

		rect {
			stroke : $orange;;
		}
	}

	@media screen and (min-width : $break-table) {

	}

	@media screen and (min-width : $break-desktop) {
		background-color : $orange;
		border           : 1px solid $orange;

		&:hover {

			span {
				color : $orange;
			}
		}

		svg {
			display : none;
		}

		span {
			background-color : $orange;
			display          : block;
		}
	}
}

.burger {
	background-color : transparent;
	border           : none;
	color            : #232323;
	cursor           : pointer;
	flex-shrink      : 0;
	height           : 16px;
	margin-right     : 16px;
	padding          : 0;
	position         : relative;
	width            : 24px;

	&::before,
	&::after {
		background-color : currentColor;
		content          : "";
		height           : 2px;
		left             : 0;
		position         : absolute;
		transition       : transform 0.3s ease-in-out, top 0.3s ease-in-out;
		width            : 100%;
	}

	&:after {
		left  : unset;
		right : 0;
		width : 100%;
	}

	&::before {
		top : 0;
	}

	&::after {
		top : calc(100% - 2px);
	}

	&__line {
		background-color : currentColor;
		border-radius    : 10px;
		height           : 2px;
		left             : 0;
		position         : absolute;
		top              : 50%;
		transform        : translateY(-50%);
		transition       : transform 0.3s ease-in-out;
		width            : 100%;
	}

