}

.header__menu {
	&.active {
		height   : 100%;
		left     : 0;
		position : absolute;
		top      : 0;
		width    : 100%;
		z-index  : 10;
	}
}

.header-menu__overlay {

	background : rgba(0, 0, 0, 0.85);
	bottom     : 0;
	left       : 0;
	opacity    : 0;
	position   : fixed;
	right      : 0;
	top        : 0;
	transition : opacity .6s ease, visibility .6s ease;
	visibility : hidden;
	z-index    : 100;

	&.active {
		opacity    : 1;
		visibility : visible;
	}
}

.header-menu__panel {
	background-color : #FFF;
	height           : 100svh;
	left             : -100%;
	max-width        : 320px;
	padding          : 0 16px;
	position         : absolute;
	top              : 0;
	transition       : left .3s ease-in;
	width            : 100%;
	z-index          : 100;

	@media screen and (min-width : $break-table) {

	}

	@media screen and (min-width : $break-desktop) {
		padding : 0 32px;
	}

	&.active {
		left       : 0;
		transition : left .3s ease-in;
	}
}

.header-menu__top {
	border-bottom : 1px solid rgba(0, 0, 0, 0.12);
	display       : flex;
	padding       : 16px 0;

	@media screen and (min-width : $break-table) {

	}

	@media screen and (min-width : $break-desktop) {
		padding : 28px 0;
	}
}

.header-menu__close {
	background-color : transparent;
	border           : none;
	margin-right     : 12px;
	padding          : 0;

	svg {
		pointer-events : none;
	}
}

.header-menu__logo {
	height : 29px;
	width  : 100px;
}

.header-menu__wrap {
	align-items     : center;
	display         : flex;
	flex-direction  : column;
	height          : calc(100% - 40px);
	justify-content : center;
	overflow-y      : auto;
	padding         : 20px 0;
	text-align      : center;


	@media screen and (min-width : $break-table) {

	}

	@media screen and (min-width : $break-desktop) {
		align-items     : flex-start;
		height          : calc(100% - 80px);
		justify-content : space-between;
		padding-bottom  : 32px;
		padding-top     : 48px;
		text-align      : start;
	}
}

.header-menu__nav {
	display        : flex;
	flex-direction : column;

	li {
		&:not(:last-child) {
			margin-bottom : 12px;
			@media screen and (min-width : $break-desktop) {
				margin-bottom : 29px;
			}
		}
	}
}

.header-menu__link {
	color       : #232323;
	font-size   : 14px;
	font-weight : 500;
	line-height : 22px;
	position    : relative;

	@media screen and (min-width : $break-desktop) {
		font-size   : 16px;
		line-height : 22px;
	}

	&:before {
		bottom     : -4px;
		content    : "";
		height     : 2px;
		position   : absolute;
		transition : width .3s ease;
		width      : 0;
		z-index    : 1;

		@media screen and (min-width : $break-desktop) {
			bottom : -10px;
		}
	}

	&:hover {
		&:before {
			transition : width .3s ease;
			width      : 100%;
		}
	}

	&--home {
		&:before {
			background-color : $blue;
		}
	}

	&--data {
		&:before {
			background-color : $dark-blue;
		}
	}

	&--marketing {
		&:before {
			background-color : $orange;
		}
	}
}

.header-menu__bottom {
	.b-btn {
		display : none;

		@media screen and (min-width : $break-desktop) {
			display : flex;
		}
	}
}

.menu-contacts {

	&:not(:last-child) {
		margin-bottom : 32px;
	}

	&__title {
		color         : rgba(35, 35, 35, 0.6);
		font-size     : 14px;
		font-weight   : 500;
		line-height   : 19px;
		margin-bottom : 12px;
	}

	&__list {

		@media screen and (min-width : $break-desktop) {
			margin-bottom : 12px;
		}
	}

	&__item {

		&:not(:last-child) {
			margin-bottom : 12px;
		}

		a {
			color       : #232323;
			font-size   : 18px;
			font-weight : 700;
			line-height : 28px;
		}

		&--none {
			@media screen and (min-width : $break-desktop) {
				display : none;
			}
		}
	}

	&--nav {
		.menu-contacts__title {

			@media screen and (min-width : $break-desktop) {
				display : none;
			}
		}
	}
}

.socials--menu {
	justify-content : center;

	@media screen and (min-width : $break-desktop) {
		justify-content : flex-start;
	}
}

.socials--data {
	.socials__button {
		background-color : $dark-blue;
	}
}

.socials--marketing {
	.socials__button {
		background-color : $orange;
	}
}
