.presentation {
	background-repeat : no-repeat;
	background-size   : cover;
	padding           : 46px 0;

	@media screen and (min-width : $break-table) {
		padding : 64px 0;
	}

	@media screen and (min-width : $break-desktop) {
		padding : 77px 0;
	}
}

.presentation__title {
	color         : $white;
	font-size     : 24px;
	font-weight   : 700;
	line-height   : 32px;
	margin-bottom : 32px;
	max-width     : 200px;

	@media screen and (min-width : $break-table) {
		font-size     : 40px;
		line-height   : 48px;
		margin-bottom : 96px;
		max-width     : 326px;
	}

	@media screen and (min-width : $break-desktop) {
		margin-bottom : 188px;

	}
}

.presentation__info {
	width : 100%;

	@media screen and (min-width : $break-table) {
		align-items     : flex-end;
		display         : flex;
		justify-content : space-between;
		margin-left     : auto;
		width           : 63%;
	}

	@media screen and (min-width : $break-desktop) {
		width : 60%;
	}
}

.presentation__wrap {
	display        : flex;
	flex-direction : column;
	position       : relative;

	@media screen and (min-width : $break-table) {

	}

	@media screen and (min-width : $break-desktop) {

	}
}

.presentation__subtext {
	color         : $white;
	font-size     : 14px;
	font-weight   : 500;
	line-height   : 22px;
	margin-bottom : 8px;
	max-width     : 300px;
	opacity       : 0.5;

	@media screen and (min-width : $break-table) {
		font-size     : 16px;
		line-height   : 24px;
		margin-bottom : 21px;
		max-width     : 401px;
	}

	@media screen and (min-width : $break-desktop) {

	}
}

.presentation__text {
	color         : $white;
	font-size     : 24px;
	font-weight   : 700;
	line-height   : 32px;
	margin-bottom : 32px;
	max-width     : 260px;

	@media screen and (min-width : $break-table) {
		font-size     : 40px;
		line-height   : 48px;
		margin-bottom : 0;
		max-width     : 450px;
	}

	@media screen and (min-width : $break-desktop) {

	}
}

.swiper--presentation {
	position : relative;

	.swiper-button {
		svg {
			circle {
				fill : transparent;
			}

			path {
				fill : $white;
			}
		}

		@media screen and (min-width : $break-table) {
			display : none;
		}

		@media screen and (min-width : $break-desktop) {
			display : flex;
		}
	}

	.swiper-pagination {
		bottom         : 70px;
		display        : none;

		flex-direction : column;

		@media screen and (min-width : $break-table) {
			display : flex;
			left    : 40px;
		}

		@media screen and (min-width : $break-desktop) {
			left : 379px;
		}
	}

	.swiper-pagination-bullet {
		background : transparent;
		height     : auto;
		width      : auto;


		&:not(:last-child) {
			margin-bottom : 16px;
		}

		span {
			color       : #FFF;
			display     : block;
			font-size   : 16px;
			font-weight : 700;
			line-height : 24px;
			position    : relative;


			&:before {
				background-color : #0E72BB;
				border-radius    : 100%;
				content          : "";
				display          : inline-block;
				height           : 16px;
				margin-right     : 12px;
				opacity          : 0;
				width            : 16px;
			}
		}
	}

	.swiper-pagination-bullet-active {
		background : transparent;
		height     : auto;
		width      : auto;

		span {
			&:before {
				opacity : 1;
			}
		}

	}
}

.progress {
	height        : 2px;
	margin-bottom : 24px;
	min-width     : 100%;

	@media screen and (min-width : $break-table) {
		min-width : 370px;
	}

	@media screen and (min-width : $break-desktop) {
		min-width : 570px;
	}
}

.progress__bar {
	background : rgba(255, 255, 255, 0.5);
	height     : 100%;
	left       : 0;
	opacity    : 1;
	position   : relative;
	right      : 0;
	width      : auto;
}

.progress__bar:after {
	background : #FFF;
	content    : "";
	height     : 100%;
	left       : 0;
	position   : absolute;
	top        : 0;
	width      : 0;
}

.progress.animate .progress__bar:after {
	transition          : width linear;
	transition-duration : 5s;
	width               : 100%;
}
