// Arrow
.swiper-button-prev,
.swiper-button-next {
	cursor                      : pointer;
	display                     : inline-block;
	flex                        : 0 0 46px;
	height                      : 20px;
	outline                     : 0;
	position                    : relative;
	-webkit-tap-highlight-color : transparent;
	transition                  : opacity .3s ease;
	user-select                 : none;
	vertical-align              : top;
	width                       : 46px;

	&:after {
		content  : '';
		left     : 0;
		position : absolute;
		top      : 0;
	}

	@media screen and (min-width : 1076px) {
		&:hover {
			opacity : .7;
		}
	}

	&.swiper-button-disabled {
		cursor         : auto;
		opacity        : 0.3;
		pointer-events : none;
	}
}

.swiper-button-prev {
	margin-right : 25px;

}

.swiper-button-next {
	margin-left : 25px;
}

.slider__pagination {
	display         : flex;
	flex-flow       : row nowrap;
	justify-content : space-between;
}

.swiper-button {
	cursor   : pointer;
	height   : 32px;
	position : unset;
	width    : 32px;
	z-index  : 2;
	@media screen and (min-width : $break-table) {
		height : 48px;
		width  : 48px;
	}

	@media screen and (min-width : $break-desktop) {

	}

	& + & {
		margin-left : 8px;

		@media screen and (min-width : $break-table) {
			margin-left : 12px;
		}
	}

	svg {
		height : 32px;
		width  : 32px;
		@media screen and (min-width : $break-table) {
			height : 48px;
			width  : 48px;
		}

		circle {
			fill       : #F1F5F6;
			transition : fill .3s ease;
		}

		path {
			fill       : #1C1B1E;
			transition : fill .3s ease;
		}
	}

	&:hover {
		svg {
			circle {
				fill       : #25A4C4;
				transition : fill .3s ease;
			}

			path {
				fill       : #FFF;
				transition : fill .3s ease;
			}
		}
	}
}

.swiper-nav {
	display : flex;
}
