.data {
	padding : 23px 0;

	@media screen and (min-width : $break-table) {
		padding : 32px 0;
	}

	@media screen and (min-width : $break-desktop) {
		padding : 40px 0;
	}
}

.data--retail {
	padding-bottom : 23px;
	padding-top    : 0;

	@media screen and (min-width : $break-table) {
		margin-top     : -100px;
		padding-bottom : 32px;
	}

	@media screen and (min-width : $break-desktop) {
		padding-bottom : 40px;
	}
}

.data__head--retail {
	display        : flex;
	flex-direction : column-reverse;
	margin-bottom  : 23px;

	@media screen and (min-width : $break-table) {
		align-items     : flex-start;
		flex-direction  : row;
		justify-content : space-between;
		margin-bottom   : 40px;
	}

	@media screen and (min-width : $break-desktop) {
		margin-bottom : 45px;
	}
}

.data__head--ecom {
	display        : flex;
	flex-direction : column-reverse;
	margin-bottom  : 23px;

	@media screen and (min-width : $break-table) {
		align-items     : flex-start;
		flex-direction  : row-reverse;
		justify-content : space-between;
		margin-bottom   : 40px;
	}

	@media screen and (min-width : $break-desktop) {
		margin-bottom : 45px;
	}

	.data__img {
		@media screen and (min-width : $break-table) {
			padding-right : 133px;
		}

		@media screen and (min-width : $break-desktop) {
			padding-right : 0;
			width         : auto;
		}
	}

	.data__wrap {

		@media screen and (min-width : $break-desktop) {
			width : auto;
		}
	}
}

.data__head--telecom {
	display        : flex;
	flex-direction : column-reverse;
	margin-bottom  : 23px;

	@media screen and (min-width : $break-table) {
		align-items     : flex-start;
		flex-direction  : row-reverse;
		justify-content : space-between;
		margin-bottom   : 40px;
	}

	@media screen and (min-width : $break-desktop) {
		margin-bottom : 45px;
	}

	.data__img {
		@media screen and (min-width : $break-table) {
			padding-right : 133px;
		}

		@media screen and (min-width : $break-desktop) {
			padding-right : 0;
			width         : auto;
		}
	}

	.data__wrap {

		@media screen and (min-width : $break-desktop) {
			width : auto;
		}
	}
}

.data__head {

	margin-bottom : 23px;

	@media screen and (min-width : $break-table) {

		margin-bottom : 40px;
	}

	@media screen and (min-width : $break-desktop) {
		margin-bottom : 45px;
	}
}

.data__img {
	margin-bottom : 24px;


	@media screen and (min-width : $break-table) {
		margin-bottom : 0;
		width         : 35%;
	}

	@media screen and (min-width : $break-desktop) {

	}

	img {
		object-fit : cover;
		width      : 100%;
	}
}

.data__wrap {
	width : 100%;

	@media screen and (min-width : $break-table) {
		width : 50%;
	}
}

.data__title {
	margin-bottom : 23px;

	@media screen and (min-width : $break-table) {

	}

	@media screen and (min-width : $break-desktop) {
		margin-bottom : 36px;
	}
}

.data__text {
	color       : #232323;
	font-size   : 14px;
	font-weight : 500;
	line-height : 22px;
	opacity     : 0.5;

	@media screen and (min-width : $break-table) {
		font-size   : 16px;
		line-height : 24px;
	}

	@media screen and (min-width : $break-desktop) {
		max-width : 406px;
	}
}

.data__name {
	color         : #232323;
	font-size     : 16px;
	font-weight   : 700;
	line-height   : 24px;
	margin-bottom : 16px;

	@media screen and (min-width : $break-table) {
		font-size     : 24px;
		line-height   : 32px;
		margin-bottom : 24px;
	}

	@media screen and (min-width : $break-desktop) {

	}
}

.data__wrapper {
	position : relative;

	.swiper-nav {
		position : absolute;
		right    : 0;
		top      : 0;

		@media screen and (min-width : $break-table) {
			bottom : 0;
			top    : unset;
		}

		@media screen and (min-width : $break-desktop) {
			display : none;
		}
	}

}

.data__grid {
	position : relative;

	.data__buttons {
		align-items     : flex-start;
		border          : none;
		bottom          : 0;
		display         : flex;
		flex-direction  : row;
		gap             : 21px;
		height          : auto;
		justify-content : space-between;
		left            : 0;
		padding         : 0;
		position        : absolute;
		width           : 100%;
		z-index         : 1;

		.b-btn {
			padding : 8px 24px 10px 24px;
			width   : 100%;

			@media screen and (min-width : $break-table) {
				min-width : 169px;
			}
		}

		@media screen and (min-width : $break-table) {
			width : auto;
		}

		@media screen and (min-width : $break-desktop) {
			display        : none;
			flex-direction : column;
			position       : unset;
		}
	}
}

.data__grid--secondary {
	.data__buttons {
		align-items     : flex-start;
		border          : none;
		bottom          : 0;
		display         : flex;
		flex-direction  : row;
		gap             : 21px;
		height          : auto;
		justify-content : space-between;
		left            : 0;
		padding         : 0;
		position        : absolute;
		z-index         : 1;

		.b-btn {
			padding : 8px 24px 10px 24px;
			width   : 100%;
		}

		@media screen and (min-width : $break-table) {

		}

		@media screen and (min-width : $break-desktop) {
			display : none;
		}
	}

	.swiper--retail {
		.swiper-slide {
			&:last-child {
				grid-column : 6 span;
			}
		}
	}

	.swiper-slide--buttons {

		@media screen and (min-width : $break-desktop) {
			align-items : center;
			display     : flex;

		}

		.data__buttons {
			display        : flex;
			flex-direction : column;
			height         : 100%;
			position       : unset;

			.b-btn {
				min-width : 169px;
			}
		}
	}
}

.data__item {
	display        : flex;
	flex-direction : column;
	height         : 100%;
}

.data__about {
	color         : #232323;
	font-size     : 13.8366px;
	font-weight   : 500;
	line-height   : 24px;
	margin-bottom : 24px;
	max-width     : 209px;

	@media screen and (min-width : $break-table) {
		font-size     : 16px;
		line-height   : 28px;
		margin-bottom : 46px;
	}

	@media screen and (min-width : $break-desktop) {
		font-size     : 14px;
		line-height   : 24px;
		margin-bottom : 40px;
	}
}

.data__num {
	color       : #0E72BB;
	font-size   : 47px;
	font-weight : 500;
	line-height : 47px;
	margin-top  : auto;

	@media screen and (min-width : $break-table) {
		font-size   : 56.2005px;
		line-height : 56px;
	}

	@media screen and (min-width : $break-desktop) {
		font-size   : 48px;
		line-height : 48px;
	}
}

.swiper--retail {
	grid-column    : 12 span;
	padding-bottom : 60px;

	@media screen and (min-width : $break-desktop) {
		padding-bottom : 0;
	}

	.swiper-wrapper {
		@media screen and (min-width : $break-table) {

		}

		@media screen and (min-width : $break-desktop) {
			box-sizing            : border-box;
			display               : grid;
			grid-template-columns : repeat(12, 1fr);
		}
	}

	.swiper-slide {
		border         : 1px solid #D3D3D3;
		display        : flex;
		flex-direction : column;
		height         : auto;
		padding        : 24px 24px;
		width          : 100%;

		@media screen and (min-width : $break-table) {
			width : 50%;
		}

		@media screen and (min-width : $break-desktop) {
			border         : none;
			display        : flex;
			flex-direction : column;
			grid-column    : 3 span;
			padding        : 48px 24px;
			width          : 100%;
		}

		&:nth-of-type(1) {
			@media screen and (min-width : $break-desktop) {
				border : 1px solid #D3D3D3;
			}
		}

		&:nth-of-type(2) {
			@media screen and (min-width : $break-desktop) {
				border-bottom : 1px solid #D3D3D3;
				border-right  : 1px solid #D3D3D3;
				border-top    : 1px solid #D3D3D3;
			}
		}

		&:nth-of-type(3) {
			@media screen and (min-width : $break-desktop) {
				border-bottom : 1px solid #D3D3D3;
				border-right  : 1px solid #D3D3D3;
				border-top    : 1px solid #D3D3D3;
			}
		}

		&:nth-of-type(4) {
			@media screen and (min-width : $break-desktop) {
				border-bottom : 1px solid #D3D3D3;
				border-right  : 1px solid #D3D3D3;
				border-top    : 1px solid #D3D3D3;
			}
		}

		&:nth-of-type(5) {
			@media screen and (min-width : $break-desktop) {
				border-bottom : 1px solid #D3D3D3;
				border-left   : 1px solid #D3D3D3;
				border-right  : 1px solid #D3D3D3;
				border-top    : 0;
			}
		}

		&:nth-of-type(6) {
			@media screen and (min-width : $break-desktop) {
				border-bottom : 1px solid #D3D3D3;
				border-right  : 1px solid #D3D3D3;
				border-top    : 0;
			}
		}

		&:nth-of-type(7) {
			@media screen and (min-width : $break-desktop) {
				border-bottom : 1px solid #D3D3D3;
				border-right  : 1px solid #D3D3D3;
				border-top    : 0;
			}
		}

		&:last-child {
			@media screen and (min-width : $break-desktop) {
				border : none;
			}
		}
	}

	.swiper-slide-next {
		@media screen and (min-width : $break-table) {
			border-left : none;
		}
	}

	.swiper-slide--buttons {
		border  : none;
		display : none;

		@media screen and (min-width : $break-desktop) {
			align-items     : center;
			display         : flex;
			justify-content : center;
		}

		.data__buttons {
			display        : flex;
			flex-direction : column;
			max-height     : 120px;

			.b-btn {
				min-width : 169px;
			}
		}
	}
}

.swiper-nav--data {
	.swiper-button {
		&:hover {
			svg {
				circle {
					fill : #0E72BB;
				}
			}
		}
	}
}
