.achievements {
	background-color : rgba(37, 164, 196, 0.08);
	padding          : 24px 0;

	@media screen and (min-width : $break-table) {
		padding : 32px 0;
	}

	@media screen and (min-width : $break-desktop) {
		padding : 40px 0;
	}
}

.achievements__grid {
	display               : grid;
	grid-template-columns : repeat(4, 1fr);

	@media screen and (min-width : $break-table) {
		grid-template-columns : repeat(12, 1fr);
	}

	@media screen and (min-width : $break-desktop) {
		align-items : center;

	}
}

.achievements__main {
	grid-column : 4 span;

	@media screen and (min-width : $break-table) {
		grid-column : 1/7;
	}

	@media screen and (min-width : $break-desktop) {
		grid-column : 3 span;
	}
}

.achievements__title {
	margin-bottom : 15px;

	@media screen and (min-width : $break-table) {
		margin-bottom : 24px;
	}

	@media screen and (min-width : $break-desktop) {
		margin-bottom : 36px;
	}
}

.achievements__text {
	color       : #232323;
	font-size   : 14px;
	font-weight : 500;
	line-height : 22px;

	@media screen and (min-width : $break-table) {
		font-size   : 16px;
		line-height : 24px;
	}

	@media screen and (min-width : $break-desktop) {

	}
}

.achievements__img {
	grid-column : 4 span;

	@media screen and (min-width : $break-table) {
		grid-column : 1/13;
		margin-top  : -239px;
		text-align  : right;
	}

	@media screen and (min-width : $break-desktop) {
		grid-column : 9 span;
		margin-top  : 0;
	}
}
