// -----------------------------------------------
//  Button config
// -----------------------------------------------

$btn-font-weight           : 400;
$btn-line-height           : 24px;
$btn-border-width          : 4px;
$btn-border-color          : $green;
$btn-padding               : 20px 24px 20px 27px;
$btn-font-size             : 14px;
$btn-border-radius         : 0;
$btn-transition            : background .3s ease, color .3s ease;

// OUTLINE

// SIZE
// Big
$btn-padding-big           : 14px 30px;
$btn-font-size-big         : 16px;
// Small
$btn-padding-small         : 8px 30px;
$btn-font-size-small       : 13px;

// COLOR

// Blue
$btn-blue-bg               : #25A4C4;
$btn-blue-color            : #FFF;
$btn-blue-color-outline    : $btn-blue-bg;
$btn-blue-bg-hover         : #FFF;
$btn-blue-color-hover      : #25A4C4;

// White
$btn-white-bg              : #FFF;
$btn-white-color           : #25A4C4;
$btn-white-color-outline   : #FFF;
$btn-white-bg-hover        : #25A4C4;
$btn-white-color-hover     : #FFF;


// LOADING
$btn-loading-color         : #FFF;
$btn-loading-color-outline : #000;
// SPACING
$btn-spacing-x             : 5px;
$btn-spacing-y             : 5px;

// -----------------------------------------------
//  Button style
// -----------------------------------------------


.b-btn {
	align-items        : center;
	border-radius      : $btn-border-radius;
	-moz-box-sizing    : border-box;
	-webkit-box-sizing : border-box;
	box-sizing         : border-box;
	display            : flex;
	justify-content    : center;
	line-height        : $btn-line-height;
	position           : relative;
	text-align         : center;
	text-decoration    : none;
	transition         : $btn-transition;


	&--secondary {
		clip-path : polygon(0 0, 91% 0, 100% 34%, 100% 100%, 9% 100%, 0 66%);
		display   : block;
		width     : fit-content;

		span {
			align-items     : center;
			bottom          : 4px;
			clip-path       : polygon(0 0, 91% 0, 100% 34%, 100% 100%, 9% 100%, 0 66%);
			display         : flex;
			font-size       : 16px;
			justify-content : center;
			line-height     : 24px;
			padding         : 8px 39px 10px 39px;
			transition      : background-color .3s ease;


			@media screen and (min-width : $break-table) {

			}
			@media screen and (min-width : $break-desktop) {
				padding : 16px 40px;

			}
		}
	}

	&--main {
		background-color : $dark-blue;
		border           : 1px solid $dark-blue;
		border-radius    : 4px;
		color            : $white;

		padding          : 8px 43px 10px 43px;

		&:hover,
		&:focus-within,
		&:active,
		&.active {
			background-color : $white;
			color            : $dark-blue;
		}
	}

	&--outline {
		background-color : $white;
		border           : 1px solid $dark-blue;
		border-radius    : 4px;
		color            : $dark-blue;
		padding          : 8px 43px 10px 43px;

		&:hover,
		&:focus-within,
		&:active,
		&.active {
			background-color : $dark-blue;
			color            : $white;
		}
	}

	&:hover,
	&:focus-within,
	&.focus {
		outline : 0;
	}

	&.disabled,
	&:disabled {
		background : inherit;
		box-shadow : none;
		cursor     : default;
		opacity    : .45;
	}

	&:active,
	&.active {

	}


	&--orange {
		background-color : $orange;
		border           : 1px solid $orange;
		color            : $white;
		padding          : 8px 43px 10px 43px;

		&:hover,
		&:focus-within,
		&:active,
		&.active {
			background-color : transparent;
			color            : $orange;
		}
	}

	&--orange-outline {
		background-color : $white;
		border           : 1px solid $orange;
		border-radius    : 4px;
		color            : $orange;
		padding          : 8px 43px 10px 43px;

		&:hover,
		&:focus-within,
		&:active,
		&.active {
			background-color : $orange;
			color            : $white;
		}
	}

	&--orange-secondary {
		background-color : $orange;
		border           : 1px solid $orange;
		color            : $btn-blue-color;

		&:hover,
		&:focus-within,
		&:active,
		&.active {
			span {
				background-color : #171717;
				color            : $orange;
			}
		}

		&.disabled,
		&:disabled {
			&:hover,
			&:focus-within,
			&:active,
			&.active {
				background-color : $orange;
				color            : $btn-blue-color;
			}
		}

		span {
			background-color : $orange;

			@media screen and (min-width : $break-table) {

			}
			@media screen and (min-width : $break-desktop) {

			}
		}
	}

	&--orange-white {
		background-color : $orange;
		border           : 1px solid $orange;
		color            : $btn-blue-color;

		&:hover,
		&:focus-within,
		&:active,
		&.active {
			span {
				background-color : $white;
				color            : $orange;
			}
		}

		&.disabled,
		&:disabled {
			&:hover,
			&:focus-within,
			&:active,
			&.active {
				background-color : $orange;
				color            : $btn-blue-color;
			}
		}

		span {
			background-color : $orange;

			@media screen and (min-width : $break-table) {

			}
			@media screen and (min-width : $break-desktop) {

			}
		}
	}

	&--transparent {
		background-color : transparent;
		border           : 1px solid $white;
		border-radius    : 4px;
		color            : $white;
		padding          : 12px 12px;

		svg {
			path {
				stroke     : $white;
				transition : stroke .3s ease;
			}
		}

		&:hover,
		&:focus-within,
		&:active,
		&.active {
			background-color : $white;
			color            : $orange;

			svg {
				path {

					stroke : $orange;
				}
			}
		}
	}

	[class^="b-icon-"],
	[class*=" b-icon-"] {
		margin-right   : 10px;
		vertical-align : middle;
	}

	// SIZE
	// Big
	&--big {
		font-size : $btn-font-size-big;
		padding   : $btn-padding-big;
	}

	&--small {
		font-size : $btn-font-size-small;
		padding   : $btn-padding-small;
	}

	// COLOR
	// Blue
	&--blue {
		background-color : $btn-blue-bg;
		border           : 1px solid #25A4C4;
		color            : $btn-blue-color;

		&:hover,
		&:focus-within,
		&:active,
		&.active {
			color : $btn-blue-color-hover;

			span {
				background-color : $btn-blue-bg-hover;
				color            : $btn-blue-color-hover;
			}
		}

		&.disabled,
		&:disabled {
			&:hover,
			&:focus-within,
			&:active,
			&.active {
				background-color : $btn-blue-bg;
				color            : $btn-blue-color;
			}
		}

		span {
			background-color : $btn-blue-bg;

			@media screen and (min-width : $break-table) {

			}
			@media screen and (min-width : $break-desktop) {

			}
		}
	}

	// White
	&--white {
		background-color : $btn-white-bg;
		border           : 1px solid #FFF;
		color            : $btn-white-color;

		&:hover,
		&:focus-within,
		&:active,
		&.active {
			span {
				background-color : $btn-white-bg-hover;
				color            : $btn-white-color-hover;
			}
		}

		&.disabled,
		&:disabled {
			&:hover,
			&:focus-within,
			&:active,
			&.active {
				background-color : $btn-blue-bg;
				color            : $btn-blue-color;
			}
		}

		span {
			background-color : $btn-white-bg;

			@media screen and (min-width : $break-table) {

			}
			@media screen and (min-width : $break-desktop) {

			}
		}
	}

	// LOADING
	&--loading {
		&:before {
			animation        : loadingSpin 1s infinite linear;
			border           : 3px solid rgba($btn-loading-color, 0.6);
			border-radius    : 50%;
			border-top-color : $btn-loading-color;
			content          : '';
			display          : inline-block;
			height           : 12px;
			margin-left      : -15px;
			margin-right     : 10px;
			transition       : border .3s ease;
			vertical-align   : middle;
			width            : 12px;
		}

		&.outline {
			&:before {
				border           : 3px solid rgba($btn-loading-color-outline, 0.2);
				border-top-color : $btn-loading-color-outline;
			}

			&:hover {
				&:before {
					border           : 3px solid rgba($btn-loading-color, 0.6);
					border-top-color : $btn-loading-color;
				}
			}
		}
	}
}

.b-btn-grouping {
	display : inline-block;

	.b-btn {
		border-radius : 0;
		float         : left;
		margin-right  : 0;

		&:first-child {
			border-radius : $btn-border-radius 0 0 $btn-border-radius;
		}

		&:last-child {
			border-radius : 0 $btn-border-radius $btn-border-radius 0;
		}

		&.outline {
			margin-left : -1px;
			z-index     : 1;

			&:hover {
				z-index : 2;
			}

			&.active {
				z-index : 3;
			}

			&:first-child {
				margin-left : 0;
			}
		}
	}
}


@keyframes pulse {
	0% {
		opacity : 0;
	}


	50% {
		opacity : 1;
	}

	100% {
		opacity : 0;
	}
}

// Future-proof disabling of clicks on `<a>` elements
a.b-btn.disabled,
fieldset[disabled] a.b-btn {
	pointer-events : none;
}

// Specificity overrides
input[type="submit"],
input[type="reset"],
input[type="button"] {
	&.full-size {
		width : 100%;
	}
}
