.production {
	display        : flex;
	flex-direction : column;

	@media screen and (min-width : $break-table) {

	}

	@media screen and (min-width : $break-desktop) {
		height : calc(100vh - 84px);
	}
}

.production__wrapper {
	display        : flex;
	flex-direction : column-reverse;
	height         : 100%;

	@media screen and (min-width : $break-table) {

	}

	@media screen and (min-width : $break-desktop) {
		flex-direction : row;
	}
}

.production__left {
	background-color  : #171717;
	background-image  : url("../images/production/hero/left_panel.png");
	background-repeat : no-repeat;
	background-size   : cover;
	display           : flex;
	flex-direction    : column;
	height            : 100%;
	padding           : 36px 15px;

	@media screen and (min-width : $break-table) {
		background-image : url("../images/production/hero/left_panel-desktop.png");
		flex-direction   : row;
		justify-content  : space-between;
		padding          : 36px 41px;
	}

	@media screen and (min-width : $break-desktop) {
		background-image : url("../images/production/hero/left_panel.png");
		flex-direction   : column;
		min-width        : 340px;
		padding          : 48px 44px;
	}
}

.production__text {
	color          : rgba(255, 255, 255, 0.67);
	font-size      : 20px;
	font-weight    : 500;
	line-height    : 24px;
	margin-bottom  : 24px;
	text-transform : uppercase;

	@media screen and (min-width : $break-table) {
		margin-bottom : 0;
	}

	p {

		&:nth-child(1) {
			max-width : 156px;
		}

		&:nth-child(2) {
			margin-left : 98px;
			max-width   : 150px;
		}

		&:nth-child(3) {
			margin-top : 4px;
			max-width  : 200px;
		}

		&:nth-child(4) {
			margin-left : 140px;
			margin-top  : 4px;
		}
	}
}

.production__buttons {
	align-items    : center;
	display        : flex;
	flex-direction : column;
	gap            : 24px;

	@media screen and (min-width : $break-table) {
		justify-content : end;
	}

	@media screen and (min-width : $break-desktop) {

	}

	.b-btn {
		min-width : 238px;

		@media screen and (min-width : $break-table) {
			min-width : 253px;
		}
	}

	.b-btn--orange {
		padding : 15px 20px;
	}

	.b-btn--transparent {
		svg {
			margin-right : 4px;
		}
	}
}

.production__main {
	background-image  : url("../images/production/hero/main.jpg");
	background-repeat : no-repeat;
	background-size   : cover;
	display           : flex;
	flex-direction    : column;
	height            : 100%;
	justify-content   : center;
	padding           : 96px 15px 135px 15px;
	position          : relative;
	width             : 100%;

	@media screen and (min-width : $break-table) {
		padding : 117px 44px 191px 44px;
	}

	@media screen and (min-width : $break-desktop) {
		padding : 0 0 0 90px;
	}
}

.production__title {
	color         : $white;
	font-family   : 'Proxon';
	font-size     : 42px;
	font-weight   : 400;
	line-height   : 42px;
	margin-bottom : 24px;
	max-width     : 300px;

	@media screen and (min-width : $break-table) {
		font-size     : 64px;
		font-weight   : 400;
		line-height   : 64px;
		margin-bottom : 48px;
		max-width     : 454px;
	}

	@media screen and (min-width : $break-desktop) {

	}

	span {
		text-transform : uppercase;
	}
}

.production__about {
	color       : #FFF;
	font-size   : 18px;
	font-weight : 600;
	line-height : 28px;
	opacity     : 0.8;

	@media screen and (min-width : $break-table) {
		font-size   : 28px;
		line-height : 36px;
	}

	@media screen and (min-width : $break-desktop) {

	}
}

.production__play {
	background-color : transparent;
	border           : none;
	bottom           : 32px;
	height           : 48px;
	left             : 16px;
	padding          : 0;
	position         : absolute;
	width            : 48px;

	@media screen and (min-width : $break-table) {
		bottom : 64px;
		left   : 44px;
	}

	@media screen and (min-width : $break-desktop) {
		bottom : 48px;
		left   : 90px;
	}

	svg {
		pointer-events : none;
	}
}

.production__socials {
	bottom         : 32px;
	display        : flex;
	flex-direction : column;
	gap            : 8px;
	position       : absolute;
	right          : 15px;

	@media screen and (min-width : $break-table) {
		bottom : 64px;
		right  : 44px;
	}

	@media screen and (min-width : $break-desktop) {
		bottom    : unset;
		top       : 50%;
		transform : translateY(-50%);
	}

	li {
		a {
			align-items     : center;
			background      : rgba(255, 255, 255, 0.25);
			border-radius   : 80px;
			display         : flex;
			height          : 40px;
			justify-content : center;
			width           : 40px;

			svg {
				pointer-events : none;
			}
		}
	}
}
