.about {
	background-image    : url("../images/data/about/about-bg-mob.png");
	background-position : center;
	background-repeat   : no-repeat;
	background-size     : contain;
	padding             : 68px 0;

	@media screen and (min-width : 490px) {
		padding : 45px 0;
	}

	@media screen and (min-width : $break-table) {
		background-image    : url("../images/data/about/about-bg.png");
		background-position : center -18px;
		background-repeat   : no-repeat;
		background-size     : 1273px 541px;
		padding             : 40px 0 200px 0;
	}

	@media screen and (min-width : $break-desktop) {
		background-position : center;
		background-size     : 1440px 541px;
	}
}

.about__wrapper {
	left     : -3px;
	position : relative;
}

.about__item {

}

.about__main {
	background-color : #F5F6F7;
	border-radius    : 12px;
	margin           : 0 auto;
	padding          : 24px 24px;

	@media screen and (min-width : $break-table) {
		max-width : 680px;
		padding   : 46px 46px;
	}

	@media screen and (min-width : $break-desktop) {
		border-radius : 17px;
		max-width     : 780px;
		padding       : 64px 64px;
	}
}

.about__title {
	margin-bottom : 24px;

	@media screen and (min-width : $break-table) {
		margin-bottom : 45px;
	}

	@media screen and (min-width : $break-desktop) {
		margin-bottom : 64px;
	}
}

.about__list {
	align-items     : flex-start;
	display         : flex;
	flex-wrap       : wrap;
	gap             : 24px;
	justify-content : space-between;

	@media screen and (min-width : $break-table) {

	}

	@media screen and (min-width : $break-desktop) {

	}
}

.about__count {
	color         : #0E72BB;
	font-size     : 40px;
	font-weight   : 600;
	line-height   : 46px;
	margin-bottom : 12px;

	@media screen and (min-width : $break-table) {
		font-size     : 45px;
		line-height   : 46px;
		margin-bottom : 17px;
	}

	@media screen and (min-width : $break-desktop) {
		font-size     : 64px;
		line-height   : 64px;
		margin-bottom : 24px;
	}
}

.about__text {
	color       : #232323;
	font-size   : 12px;
	font-weight : 500;
	line-height : 20px;
	max-width   : 107px;

	@media screen and (min-width : $break-table) {
		font-size   : 14px;
		line-height : 24px;
		max-width   : 154px;
	}

	@media screen and (min-width : $break-desktop) {
		max-width : 182px;
	}
}
