.hero {

	&--main {
		background-color    : $blue;
		background-image    : url("../images/home/hero/main-home-mob.png");
		background-position : bottom right;
		background-repeat   : no-repeat;
		background-size     : 320px 240px;
		padding             : 29px 0 174px 0;


		@media screen and (min-width : $break-table) {
			background-image : url("../images/home/hero/main-home-tablet.png");
			background-size  : 600px;
			padding          : 200px 0 174px;
		}

		@media screen and (min-width : $break-desktop) {
			background-image    : url("../images/home/hero/main-home-desktop.png");
			background-position : center right;
			background-size     : contain;
			padding             : 139px 0 201px 0;
		}
	}

	&--data {
		background-color    : #1B2028;
		background-image    : url("../images/data/hero/hero.jpg");
		background-position : center;
		background-repeat   : no-repeat;
		background-size     : cover;
		padding             : 120px 0 185px 0;

		@media screen and (min-width : $break-table) {
			padding : 200px 0 269px 0;
		}

		@media screen and (min-width : $break-desktop) {

		}
	}

	&--marketing {
		background-image    : url("../images/marketing/hero/marketing-mob.png");
		background-position : top -237px right -123px;
		background-repeat   : no-repeat;
		background-size     : 484px 497px;
		padding             : 260px 0 67px;

		@media screen and (min-width : $break-table) {
			background-image    : url("../images/marketing/hero/marketing-tablet.png");
			background-position : center right;
			background-size     : contain;
			padding             : 124px 0 189px 0;
		}

		@media screen and (min-width : $break-desktop) {
			background-image    : url("../images/marketing/hero/marketing.png");
			background-position : center right;
			background-size     : contain;
			padding             : 135px 0 189px 0;
		}
	}
}

.hero__wrapper--marketing {
	margin    : 0 auto;
	max-width : 183px;

	@media screen and (min-width : $break-table) {
		margin-left : 0;
		max-width   : 259px;
	}

	@media screen and (min-width : $break-desktop) {
		max-width : 337px;
	}
}

.hero__title {
	color          : #FFF;
	font-family    : 'Proxon';
	font-size      : 64px;
	font-style     : normal;
	font-weight    : 400;
	line-height    : 62px;
	text-transform : uppercase;

	&--data {
		font-size     : 46px;
		line-height   : 46px;
		margin-bottom : 48px;
		max-width     : 290px;

		@media screen and (min-width : $break-table) {
			font-size   : 64px;
			line-height : 64px;
			max-width   : 454px;
		}

		@media screen and (min-width : $break-desktop) {

		}
	}

	&--marketing {
		color          : #242B33;
		font-size      : 48px;
		line-height    : 48px;
		margin-bottom  : 10px;
		max-width      : 200px;
		position       : relative;
		text-align     : right;
		text-transform : unset;

		@media screen and (min-width : $break-table) {
			font-size     : 64px;
			line-height   : 67px;
			margin-bottom : 36px;
			max-width     : none;
		}

		@media screen and (min-width : $break-desktop) {
			font-size     : 96px;
			line-height   : 88px;
			margin-bottom : 48px;
		}

		span {
			text-transform : uppercase;
		}

		&:before {
			background-color : #F30;
			content          : "";
			height           : 61px;
			position         : absolute;
			right            : -18px;
			top              : 38px;
			width            : 139px;
			z-index          : -1;

			@media screen and (min-width : $break-table) {
				height : 88px;
				right  : -46px;
				top    : 36px;
				width  : 227px;
			}

			@media screen and (min-width : $break-desktop) {
				height : 95px;
				right  : -60px;
				top    : 38px;
				width  : 334px;
			}
		}
	}
}

.hero__text {
	color       : #FFF;
	font-size   : 20px;
	font-weight : 500;
	line-height : 28px;
	opacity     : 0.8;

	@media screen and (min-width : $break-table) {
		font-size   : 28px;
		line-height : 36px;
	}

	@media screen and (min-width : $break-desktop) {

	}

	&--data {
		font-size   : 18px;
		line-height : 28px;

		@media screen and (min-width : $break-table) {
			font-size   : 26px;
			line-height : 34px;
		}

		@media screen and (min-width : $break-desktop) {

		}
	}

	&--marketing {
		color       : #232323;
		font-size   : 20px;
		line-height : 28px;
		max-width   : 331px;
		text-align  : right;

		@media screen and (min-width : $break-table) {
			font-size   : 28px;
			line-height : 36px;
			max-width   : 331px;
		}

		@media screen and (min-width : $break-desktop) {

		}
	}
}

.hero--main {
	.hero__title {
		margin-bottom : 32px;
		max-width     : 224px;

		@media screen and (min-width : $break-table) {
			margin-bottom : 48px;
		}

		@media screen and (min-width : $break-desktop) {
		}
	}

	.hero__text {
		max-width : 201px;

		@media screen and (min-width : $break-table) {
			max-width : 183px;
		}

		@media screen and (min-width : $break-desktop) {
			max-width : 248px;
		}
	}
}
