.footer {
	&--main {
		background-color : #1B2028;
	}

	&--white {
		background-color : $white;
	}
}

.footer-top {
	padding : 48px 0;

	@media screen and (min-width : $break-table) {
		padding : 64px 0 48px 0;
	}

	@media screen and (min-width : $break-desktop) {
		padding : 115px 0 144px 0;
	}
}

.footer-top__wrapper {
	display               : grid;
	grid-template-columns : repeat(4, 1fr);

	@media screen and (min-width : $break-table) {

	}

	@media screen and (min-width : $break-desktop) {
		grid-template-columns : repeat(12, 1fr);
	}
}

.footer-top__left {
	border-bottom  : 1px solid rgba(255, 255, 255, 0.2);
	grid-column    : 4 span;
	padding-bottom : 48px;

	@media screen and (min-width : $break-table) {
		padding-bottom : 64px;
	}

	@media screen and (min-width : $break-desktop) {
		border-bottom  : none;
		grid-column    : 5 span;
		padding-bottom : 0;
	}
}

.footer-top__right {
	grid-column : 4 span;
	padding-top : 48px;
	position    : relative;

	@media screen and (min-width : $break-table) {

	}

	@media screen and (min-width : $break-desktop) {
		grid-column  : 7 span;
		padding-left : 128px;
		padding-top  : 0;

		&:after {
			background-color : rgba(255, 255, 255, 0.2);
			content          : "";
			height           : calc(100% - 60px);
			left             : 0;
			position         : absolute;
			top              : 0;
			width            : 1px;
			z-index          : 1;
		}
	}
}

.footer-top__right--white {
	@media screen and (min-width : $break-desktop) {
		&:after {
			background-color : rgba(26, 26, 26, 0.2);
			content          : "";
			height           : calc(100% - 60px);
			left             : 0;
			position         : absolute;
			top              : 0;
			width            : 1px;
			z-index          : 1;
		}
	}
}

.footer-top__title {
	color         : $white;
	margin-bottom : 24px;

	@media screen and (min-width : $break-table) {
		margin-bottom : 49px;
	}

	@media screen and (min-width : $break-desktop) {
		margin-bottom : 60px;
	}

	&--white {
		color : #232323;
	}
}

.footer-contacts {

	@media screen and (min-width : $break-table) {
		align-items     : center;
		display         : flex;
		justify-content : space-between;
	}

	@media screen and (min-width : $break-desktop) {
		align-items     : flex-start;
		flex-direction  : column;
		justify-content : flex-start;
	}

	&--main {

	}

	&__item {

		&:not(:last-child) {
			margin-bottom : 24px;

			@media screen and (min-width : $break-table) {
				margin-bottom : 0;
			}

			@media screen and (min-width : $break-desktop) {
				margin-bottom : 48px;
			}
		}
	}

	&__name {
		color         : #818181;
		font-size     : 14px;
		font-weight   : 500;
		line-height   : 22px;
		margin-bottom : 12px;

		@media screen and (min-width : $break-table) {
			font-size     : 16px;
			line-height   : 24px;
			margin-bottom : 16px;
		}

		@media screen and (min-width : $break-desktop) {

		}

	}

	&__link {
		color       : $white;
		font-size   : 14px;
		font-weight : 700;
		line-height : 22px;

		@media screen and (min-width : $break-table) {
			font-size   : 18px;
			line-height : 28px;
		}

		@media screen and (min-width : $break-desktop) {

		}

		&--white {
			color : #232323;
		}
	}
}

.socials {
	align-items : flex-start;
	display     : flex;

	&--main {
		.socials__button {
			background-color : $blue;

			&.active {
				background-color : $white;
			}

			.socials__icon--active {
				path {
					fill : $blue;
				}
			}
		}
	}

	&--white {
		.socials__button {
			background-color : #0E72BB;
			border           : 1px solid $dark-blue;

			&.active {
				background-color : $white;
			}
		}

		.socials__icon--active {
			path {
				fill : $dark-blue;
			}
		}
	}

	&--marketing {
		.socials__button {
			background-color : $orange;
			border           : 1px solid $orange;

			&.active {
				background-color : $white;
			}
		}

		.socials__icon--active {
			path {
				fill : $orange;
			}
		}
	}

	&--production {
		.socials__button {
			background-color : $orange;
			border           : 1px solid $orange;

			&.active {
				background-color : transparent;
			}
		}

		.socials__icon--active {
			path {
				fill : $orange;
			}
		}
	}

	&__item {
		position : relative;

		& + & {
			margin-left : 8px;
		}
	}

	&__button {
		align-items      : center;
		background-color : transparent;
		border           : none;
		border-radius    : 100%;
		display          : flex;
		height           : 36px;
		justify-content  : center;
		transition       : background-color .3s ease;
		width            : 36px;

		@media screen and (min-width : $break-table) {
			height : 40px;
			width  : 40px;
		}

		@media screen and (min-width : $break-desktop) {

		}

		&.active {
			.socials__icon--default {
				display : none;
			}

			.socials__icon--active {
				display : block;
			}
		}
	}

	.socials__icon {
		&--defualt {
			display : block;
		}

		&--active {
			display : none;
		}
	}

	&__wrap {
		align-items      : center;
		background-color : #EBEEF3;
		border-radius    : 12px;
		display          : flex;
		flex-direction   : column;
		height           : 124px;
		left             : 0;
		opacity          : 0;
		padding          : 24px 24px;
		position         : absolute;
		top              : 46px;
		transition       : opacity .3s ease, visibility .3s ease;
		visibility       : hidden;
		width            : 250px;
		z-index          : 1;

		&.active {
			opacity    : 1;
			visibility : visible;
		}

		span {
			color         : #232323;
			display       : block;
			font-size     : 16px;
			font-weight   : 600;
			line-height   : 24px;
			margin-bottom : 10px;
			text-align    : center;
		}

		.b-btn {
			min-width : 169px;
		}
	}
}

.footer-top__secondtitle {
	color         : $white;
	margin-bottom : 16px;

	@media screen and (min-width : $break-table) {
		margin-bottom : 28px;
	}

	@media screen and (min-width : $break-desktop) {

	}

	&--white {
		color : #232323;
	}
}

.footer-top__text {
	color         : $white;
	font-size     : 14px;
	font-weight   : 500;
	line-height   : 22px;
	margin-bottom : 50px;
	max-width     : 246px;

	@media screen and (min-width : $break-table) {
		font-size     : 16px;
		line-height   : 24px;
		margin-bottom : 50px;
		max-width     : 408px;
	}

	@media screen and (min-width : $break-desktop) {

	}

	&--white {
		color   : #232323;
		opacity : 0.5;
	}
}


.footer-bottom {

	@media screen and (min-width : $break-table) {

	}

	@media screen and (min-width : $break-desktop) {
		border-top : 1px solid rgba(255, 255, 255, 0.2);
		padding    : 30px 0;
	}

	&--white {
		border-top : 1px solid rgba(0, 0, 0, 0.2);
	}
}

.footer-bottom__wrapper {
	align-items     : center;
	border-top      : 1px solid rgba(255, 255, 255, 0.2);
	display         : flex;
	justify-content : space-between;
	padding         : 16px 0;

	@media screen and (min-width : $break-desktop) {
		border-top : none;
		padding    : 0;
	}
}

.footer-bottom__link {
	color       : #818181;
	font-size   : 12px;
	font-weight : 500;
	line-height : 24px;
}

.button-top {
	background-color : transparent;
	border           : none;
	border-radius    : 100%;
	height           : 32px;
	padding          : 0;
	width            : 32px;

	@media screen and (min-width : $break-table) {
		height : 48px;
		width  : 48px;
	}

	@media screen and (min-width : $break-desktop) {

	}

	svg {
		height         : 32px;
		pointer-events : none;
		width          : 32px;

		@media screen and (min-width : $break-table) {
			height : 48px;
			width  : 48px;
		}
	}

	&--main {
		svg {
			circle {
				fill       : transparent;
				stroke     : $white;
				transition : fill .3s ease;
			}

			path {
				fill       : $white;
				transition : fill .3s ease;
			}
		}

		&:hover {
			svg {
				circle {
					fill : $white;
				}

				path {
					fill : #232323;
				}
			}
		}
	}

	&--white {
		svg {
			circle {
				fill       : transparent;
				stroke     : #0E72BB;
				transition : fill .3s ease;
			}

			path {
				fill       : #0E72BB;
				transition : fill .3s ease;
			}
		}

		&:hover {
			svg {
				circle {
					fill : #0E72BB;
				}

				path {
					fill : $white;
				}
			}
		}
	}

	&--marketing {
		svg {
			circle {
				fill       : transparent;
				stroke     : $orange;
				transition : fill .3s ease;
			}

			path {
				fill       : $orange;
				transition : fill .3s ease;
			}
		}

		&:hover {
			svg {
				circle {
					fill : $orange;
				}

				path {
					fill : $white;
				}
			}
		}
	}


}

.form--data {
	input, textarea {
		color : #232323;
	}

	.form__input {
		border-bottom : 1px solid rgba(26, 26, 26, 0.2);
	}

	.form__textarea {
		border-bottom : 1px solid rgba(26, 26, 26, 0.2);
	}

	.b-btn--main {
		font-size   : 14px;
		line-height : 22px;
		margin-top  : 38px;
		padding     : 14px 44px;

		@media screen and (min-width : $break-table) {
			font-size   : 16px;
			line-height : 24px;
			padding     : 16px 38px;
		}

		@media screen and (min-width : $break-desktop) {

		}
	}
}


.form--production {
	input, textarea {
		color : #232323;
	}

	.form__input {
		border-bottom : 1px solid rgba(255, 255, 255, 0.2);
		color         : $white;

		&::placeholder {
			color : $white;
		}
	}

	.form__textarea {
		border-bottom : 1px solid rgba(255, 255, 255, 0.2);
		color         : $white;

		&::placeholder {
			color : $white;
		}
	}


	.b-btn--secondary {
		font-size   : 14px;
		line-height : 22px;
		margin-top  : 38px;


		@media screen and (min-width : $break-table) {
			font-size   : 16px;
			line-height : 24px;

		}

		@media screen and (min-width : $break-desktop) {

		}
	}
}
