.works {
	padding : 24px 0;

	@media screen and (min-width : $break-table) {
		padding : 32px 0;
	}

	@media screen and (min-width : $break-desktop) {
		padding : 40px 0;
	}
}

.works__header {
	margin-bottom : 24px;
	@media screen and (min-width : $break-table) {
		margin-bottom : 32px;
	}

	@media screen and (min-width : $break-desktop) {
		margin-bottom : 48px;
	}
}

.works__item {
	cursor   : pointer;
	display  : block;
	margin   : 0 5px;
	position : relative;

	@media screen and (min-width : $break-table) {
		margin : 0 15px;
	}

	@media screen and (min-width : $break-desktop) {
		margin : 0 20px;
	}

	&:hover {
		.works__img {

			&:before {
				opacity : 0;
			}

			img {
				filter : grayscale(0%);
			}
		}

		.works__main {
			opacity : 1;
		}
	}
}

.works__img {
	display  : block;
	position : relative;

	&:before {
		background : linear-gradient(0deg, rgba(0, 68, 89, 0.2), rgba(0, 68, 89, 0.2)), url(.jpg);
		content    : "";
		height     : 100%;
		left       : 0;
		position   : absolute;
		top        : 0;
		transition : opacity .4s ease;
		width      : 100%;
		z-index    : 1;
	}

	img {
		filter     : grayscale(100%);
		object-fit : cover;
		transition : filter .4s ease;
		width      : 100%;
	}
}


.works__main {
	align-items     : center;
	background      : linear-gradient(0deg, rgba(0, 0, 0, 0.62), rgba(0, 0, 0, 0.62));
	display         : flex;
	flex-direction  : column;
	height          : 100%;
	justify-content : center;
	left            : 0;
	opacity         : 0;
	position        : absolute;
	top             : 0;
	transition      : opacity .3s ease;
	width           : 100%;
	z-index         : 2;
}

.works__text {
	color         : $white;
	font-size     : 14px;
	font-weight   : 600;
	line-height   : 22px;
	margin-bottom : 8px;
}

.works__name {
	color          : $white;
	font-size      : 32px;
	font-weight    : 700;
	line-height    : 32px;
	text-transform : uppercase;
}

.works__wrapper {

	overflow : hidden;
}

.swiper--works {
	margin   : 0 -10px;
	overflow : visible;

	@media screen and (min-width : $break-table) {
		margin : 0 -15px;
	}

	@media screen and (min-width : $break-desktop) {
		margin : 0 -20px;
	}

	.swiper-slide {
		width : 100%;

		@media screen and (min-width : $break-table) {
			width : 50%;
		}

		@media screen and (min-width : $break-desktop) {
			width : 25%;
		}
	}
}
