* {
	-webkit-tap-highlight-color : rgba(0, 0, 0, 0);
}

html {
	box-sizing : border-box;
	height     : 100%;
}

.black {
	background-color : #171717;
}

*,
*::before,
*::after {
	box-sizing : border-box;
}

.page-wrapper {
	display        : flex;
	flex-direction : column;
	min-height     : 100%;
	overflow       : hidden;
	position       : relative;

	@media screen and (min-width : $break-table) {

	}

	@media screen and (min-width : $break-desktop) {

	}
}

.main {
	flex : 1 0 auto;
}

.container {
	-moz-box-sizing    : border-box;
	-webkit-box-sizing : border-box;
	box-sizing         : border-box;
	margin             : 0 auto;
	max-width          : 1248px;
	padding            : 0 14px;
	width              : 100%;

	@media screen and (min-width : $break-table) {
		padding : 0 44px;
	}

	@media screen and (min-width : $break-desktop) {

	}
}

.section-title {
	color       : #232323;
	font-size   : 24px;
	font-weight : 700;
	line-height : 32px;

	@media screen and (min-width : $break-table) {
		font-size   : 40px;
		line-height : 48px;
	}

	@media screen and (min-width : $break-desktop) {

	}
}

.section-header {
	align-items     : center;
	display         : flex;
	justify-content : space-between;
}

.scroll {
	height   : 100vh;
	overflow : hidden;
	position : relative;
	width    : 100vw;
}
