.directions {
	padding : 24px 0;

	@media screen and (min-width : $break-table) {
		padding : 32px 0;
	}

	@media screen and (min-width : $break-desktop) {
		padding : 40px 0;
	}

}

.directions__header {
	margin-bottom : 24px;

	@media screen and (min-width : $break-table) {
		margin-bottom : 32px;
	}

	@media screen and (min-width : $break-desktop) {
		margin-bottom : 48px;
	}

	.swiper-nav {
		@media screen and (min-width : $break-table) {
			display : none;
		}
	}
}

.directions__title {

}

.directions__wrap {
	margin : 0 -13px;

	@media screen and (min-width : $break-table) {
		margin : 0;
	}
}

.swiper--directions {
	overflow : visible;

	.swiper-wrapper {

		@media screen and (min-width : $break-table) {
			box-sizing     : border-box;
			display        : flex;
			flex-direction : column;
			gap            : 9px;
		}

		@media screen and (min-width : $break-desktop) {
			align-items     : flex-start;
			flex-direction  : row;
			justify-content : center;
		}
	}

	.swiper-slide {

		@media screen and (min-width : $break-table) {

		}

		@media screen and (min-width : $break-desktop) {
			margin : 0 -35px;
			width  : 37%;
		}

	}
}

.directions__grid {

	@media screen and (min-width : $break-table) {
		display        : flex;
		flex-direction : column;
		gap            : 9px;
	}

	@media screen and (min-width : $break-desktop) {
		align-items     : flex-start;
		flex-direction  : row;
		justify-content : center;
	}
}

.directions__group {
	display        : flex;
	flex-direction : column;
	gap            : 8px;
	margin         : 0 13px;

	@media screen and (min-width : $break-table) {
		flex-direction  : row;
		justify-content : center;
		width           : 100%;
	}

	@media screen and (min-width : $break-desktop) {
		flex-direction : column;
		flex-wrap      : nowrap;
		gap            : 12px;
		margin         : 0;
		width          : 100%;
	}

	&--reverse {

		@media screen and (min-width : $break-table) {
			flex-direction  : row-reverse;
			justify-content : center;
		}

		@media screen and (min-width : $break-desktop) {
			flex-direction : column-reverse;
		}
	}

	&:hover {
		.directions__item--img {
			filter : grayscale(0);

			&:before {
				opacity : 0;
			}
		}
	}

}

.directions__item {
	height     : 212px;
	text-align : center;
	width      : 100%;

	@media screen and (min-width : $break-table) {
		height : 250px;
		margin : 0 -26px;
		width  : 357px;
	}

	@media screen and (min-width : $break-desktop) {
		height : 313px;
		margin : 0;
		width  : 100%;
	}

	&--one {
		background-color    : $blue;
		display             : flex;
		flex-direction      : column;
		justify-content     : center;
		mask-image          : url("../images/home/directions/directions-1.svg");
		mask-position       : 50% 50%;
		-webkit-mask-repeat : no-repeat;
		-webkit-mask-size   : cover;


		@media screen and (min-width : $break-table) {
			mask-image : url("../images/home/directions/directions-1-tablet.svg");
		}

		@media screen and (min-width : $break-desktop) {
			mask-image : url("../images/home/directions/directions-1.svg");
			padding    : 30px 0;
		}
	}

	&--img {
		background-color    : $blue;
		filter              : grayscale(100%);
		mask-image          : url("../images/home/directions/directions-2.svg");
		mask-position       : 50% 50%;
		-webkit-mask-repeat : no-repeat;
		-webkit-mask-size   : cover;
		position            : relative;
		transition          : filter .3s ease;

		&:before {
			background : linear-gradient(0deg, rgba(0, 68, 89, 0.21), rgba(0, 68, 89, 0.21));
			content    : "";
			height     : 100%;
			left       : 0;
			opacity    : 1;
			position   : absolute;
			top        : 0;
			transition : opacity .3s ease;
			width      : 100%;
			z-index    : 12;
		}

		@media screen and (min-width : $break-table) {
			mask-image : url("../images/home/directions/directions-2-tablet.svg");
		}

		@media screen and (min-width : $break-desktop) {
			mask-image : url("../images/home/directions/directions-2.svg");
		}

		img {
			height     : 313px;
			object-fit : cover;
			width      : 100%;

			@media screen and (min-width : $break-table) {
				height : 250px;
			}

			@media screen and (min-width : $break-desktop) {
				height : 313px;
			}
		}
	}
}

.directions__name {
	color         : $white;
	font-size     : 20px;
	font-weight   : 800;
	line-height   : 30px;
	margin-bottom : 12px;

	@media screen and (min-width : $break-table) {
		font-size     : 24px;
		line-height   : 32px;
		margin-bottom : 16px;
	}

	@media screen and (min-width : $break-desktop) {
		font-size   : 28px;
		line-height : 36px;
	}
}

.directions__subtitle {
	color       : $white;
	font-size   : 16px;
	font-weight : 700;
	line-height : 24px;
	margin      : 0 auto 8px auto;
	max-width   : 233px;

	@media screen and (min-width : $break-table) {
		font-size   : 18px;
		line-height : 26px;
		margin      : 0 auto 12px auto;
	}

	@media screen and (min-width : $break-desktop) {
		font-size   : 20px;
		line-height : 28px;
		max-width   : 241px;
	}
}

.directions__text {
	color       : $white;
	font-size   : 12px;
	font-weight : 500;
	line-height : 20px;
	margin      : 0 auto;
	max-width   : 228px;

	@media screen and (min-width : $break-table) {
		font-size   : 14px;
		line-height : 22px;
		max-width   : 296px;
	}

	@media screen and (min-width : $break-desktop) {
		font-size   : 16px;
		line-height : 24px;
		max-width   : 311px;
	}
}
