.partners {
	padding : 48px 0 24px 0;

	@media screen and (min-width : $break-table) {
		padding : 64px 0 32px 0;
	}

	@media screen and (min-width : $break-desktop) {
		padding : 80px 0 40px 0;
	}
}

.partners__head {
	margin-bottom : 48px;
}

.swiper-partners {


	.swiper-slide {
		text-align : center;
		width      : 33.33333%;

		@media screen and (min-width : $break-table) {
			width : 25%;
		}

		@media screen and (min-width : $break-desktop) {
			width : 20%;
		}


		img {
			object-fit : cover;
		}
	}
}
