.application {
	background-image    : url("../images/marketing/application/application-mob.jpg");
	background-position : center;
	background-repeat   : no-repeat;
	background-size     : cover;
	padding             : 48px 0;

	@media screen and (min-width : $break-table) {
		background-image : url("../images/marketing/application/application-tablet.jpg");
		padding          : 80px 0;
	}

	@media screen and (min-width : $break-desktop) {
		background-image : url("../images/marketing/application/application-desktop.jpg");
		padding          : 123px 0;
	}

	.b-btn--orange {
		font-size   : 14px;
		line-height : 24px;
		min-width   : 210px;
		padding     : 11px;

		@media screen and (min-width : $break-table) {
			font-size   : 16px;
			line-height : 24px;
			padding     : 17px;
		}

		@media screen and (min-width : $break-desktop) {


		}
	}
}

.application__wrapper {
	align-items    : center;
	display        : flex;
	flex-direction : column;
}

.application__title {
	color         : #232323;
	font-size     : 24px;
	font-weight   : 700;
	line-height   : 32px;
	margin-bottom : 16px;
	max-width     : 261px;
	text-align    : center;

	@media screen and (min-width : $break-table) {
		font-size     : 40px;
		line-height   : 48px;
		margin-bottom : 36px;
		max-width     : 437px;
	}

	@media screen and (min-width : $break-desktop) {

		margin-bottom : 48px;
		max-width     : 437px;
	}
}
