.offer {
	background-image    : url("../images/data/data/offer/offer-desktop.png");
	background-position : center 86px;
	background-repeat   : no-repeat;
	background-size     : 550px 135px;
	padding-bottom      : 120px;
	padding-top         : 40px;


	@media screen and (min-width : $break-table) {
		background-position : center 80px;
		background-size     : contain;
		padding-bottom      : 140px;
		padding-top         : 40px;
	}

	@media screen and (min-width : $break-desktop) {

	}
}

.offer__wrapper {
	display         : flex;
	justify-content : center;

	.b-btn--main {
		font-size   : 14px;
		line-height : 24px;
		padding     : 15px 47px 17px 47px;

		@media screen and (min-width : $break-table) {
			font-size   : 20px;
			line-height : 24px;
			padding     : 18px 67px 22px 67px;
		}

		@media screen and (min-width : $break-desktop) {


		}
	}
}
