
.overlay {
	//opacity: 0;
	background : rgba(0, 0, 0, 0.85);
	bottom     : 0;
	display    : none;
	left       : 0;
	position   : fixed;
	right      : 0;
	//z-index: -1;
	top        : 0;
	transition : opacity 0.5s ease;
	z-index    : 101;

	&.active {
		display : flex !important;
		/*  z-index: 9;
		opacity: 1;
		transition: opacity 0.5s ease; */
	}
}

.popup {
	display    : none;
	left       : 50%;
	max-width  : 320px;
	padding    : 0 15px;
	//z-index: -3;
	position   : fixed;
	top        : 50%;
	transform  : translate(-50%, -45%);
	transition : opacity 0.5s ease;
	//opacity: 0;
	width      : 100%;
	z-index    : 103;


	&.active {
		display    : block;
		//opacity: 1;
		transition : opacity 0.5s ease;
		//transform: translate(-50%, -45%);
		z-index    : 103;
	}

	&__wrap {
		align-items      : center;
		background-color : $white;
		border-radius    : 4px;
		display          : flex;
		flex-direction   : column;
		justify-content  : center;
		min-height       : 220px;
		overflow         : hidden;
		padding          : 30px;
		position         : relative;
	}

	&__close {
		background-color : transparent;
		border           : none;
		position         : absolute;
		right            : 28px;
		top              : 16px;
		z-index          : 999;
	}
}

