.our-works {
	padding : 32px 0 24px 0;

	@media screen and (min-width : $break-table) {
		padding : 64px 0 32px 0;
	}

	@media screen and (min-width : $break-desktop) {
		padding : 120px 0 60px 0;
	}
}

.our-works__wrapper {
	position : relative;
}

.our-works__title {
	color         : $white;
	margin-bottom : 40px;

	@media screen and (min-width : $break-table) {
		margin-bottom : 30px;
	}

	@media screen and (min-width : $break-desktop) {
		margin-bottom : 48px;
	}
}

.our-works__grid {
	display               : grid;
	grid-template-columns : repeat(4, 1fr);

	@media screen and (min-width : $break-table) {
		grid-template-columns : repeat(12, 1fr);
	}

	@media screen and (min-width : $break-desktop) {

	}
}

.our-works__item {
	display     : none;
	grid-column : 4 span;
	overflow    : hidden;
	position    : relative;

	&:nth-child(-n+3) {
		display : block;
	}

	@media screen and (min-width : $break-table) {
		grid-column : 6 span;
		&:nth-child(-n+6) {
			display : block;
		}
	}

	@media screen and (min-width : $break-desktop) {
		display     : block;
		grid-column : 3 span;
	}

	&:hover {
		.our-works__img {
			img {
				filter    : grayscale(0);
				transform : scale(1.2);
			}
		}

		.our-works__about {
			opacity : 1;
		}

		.our-works__text {
			opacity : 1;
			top     : 0;
		}

		.our-works__name {
			opacity : 1;
			top     : 0;
		}
	}
}

.our-works__img {
	img {
		filter     : grayscale(100%);
		object-fit : cover;
		transition : all 0.4s ease;
		width      : 100%;
	}
}

.our-works__about {
	background : rgba(5, 5, 5, 0.7);
	display    : table;
	height     : 102%;
	left       : -1%;
	opacity    : 0;
	position   : absolute;
	text-align : center;
	top        : -1%;
	transition : opacity 0.4s ease;
	width      : 102%;
}

.our-works__wrap {
	display        : table-cell;
	padding        : 10px;
	vertical-align : middle;
}

.our-works__text {
	color         : $white;
	font-size     : 14px;
	font-weight   : 600;
	line-height   : 22px;
	margin-bottom : 8px;
	opacity       : 0;
	position      : relative;
	top           : 20px;
	transition    : all 0.4s ease;
}

.our-works__name {
	color          : $white;
	font-size      : 32px;
	font-weight    : 700;
	line-height    : 32px;
	opacity        : 0;
	position       : relative;
	text-transform : uppercase;
	top            : 20px;
	transition     : all 0.4s ease;
}

.our-works__link {
	bottom   : 0;
	display  : block;
	left     : 0;
	position : absolute;
	right    : 0;
	top      : 0;
	z-index  : 5;
}

.our-works__btn {
	align-items      : center;
	background-color : transparent;
	border           : 1px solid $white;
	border-radius    : 4px;
	color            : #FFF;
	display          : flex;
	flex-direction   : column;
	font-size        : 14px;
	font-weight      : 700;
	grid-column      : 4 span;
	justify-content  : space-between;
	line-height      : 24px;
	margin           : 32px auto 0 auto;
	min-width        : 190px;
	padding          : 12px 0;
	text-align       : center;
	text-transform   : uppercase;
	transition       : background-color .3s ease;

	svg {
		display : none;
	}

	@media screen and (min-width : $break-table) {
		margin   : 0;
		position : absolute;
		right    : 0;
		top      : 0;
	}

	@media screen and (min-width : $break-desktop) {
		border          : 8px solid #2B2B2B;
		border-radius   : 0;
		font-size       : 20px;
		grid-column     : 3 span;
		justify-content : space-between;
		margin          : 0;
		padding         : 30px 0;
		position        : unset;
		svg {
			display : block;
		}
	}

	span {


		@media screen and (min-width : $break-desktop) {
			display   : block;
			margin    : 15px 0 22px 0;
			max-width : 120px;
		}
	}

	&:hover {
		background-color : #2B2B2B;
	}
}
