.marketing {
	padding : 23px 0;
	@media screen and (min-width : $break-table) {
		padding : 32px 0;
	}

	@media screen and (min-width : $break-desktop) {
		padding : 0;
	}
}

.marketing--white {
	.marketing__title {
		color : $white;
	}

	.marketing__text {
		color     : #FFF;
		max-width : 385px;
		opacity   : 0.5;
	}

	.marketing__about {
		color : $white;
	}

	.marketing__wrapper {
		flex-direction : column;

		@media screen and (min-width : $break-table) {

		}

		@media screen and (min-width : $break-desktop) {
			flex-direction : row;
		}
	}

	.marketing__wrapper--reverse {
		flex-direction : column-reverse;

		@media screen and (min-width : $break-table) {

		}

		@media screen and (min-width : $break-desktop) {
			flex-direction : row;
		}
	}

	.marketing__info {
		margin-bottom : 32px;
		padding-right : 0;

		@media screen and (min-width : $break-table) {
			margin-bottom : 48px;
		}

		@media screen and (min-width : $break-desktop) {
			margin-bottom : 0;
		}
	}

	.marketing__wrap {
		align-items    : flex-start;
		flex-direction : column;
		gap            : 24px;
		min-width      : 532px;
		@media screen and (min-width : $break-table) {
			gap : 48px;
		}

		@media screen and (min-width : $break-desktop) {

		}
	}

	.marketing__items {
		justify-content : space-between;
		margin-bottom   : 0;
		width           : 100%;
	}

	.marketing__item {
		width : 33%;
	}

	.marketing__about:before {
		background-color : rgba(255, 255, 255, 0.2);
		width            : 160px;
	}

	.marketing__buttons {
		.b-btn {
			width : auto;
		}

		.b-btn--orange {
			min-width : 207px;
			padding   : 11px 20px 13px 20px;

			@media screen and (min-width : $break-table) {
				min-width : 253px;
				padding   : 15px 20px 17px 20px;
			}

			@media screen and (min-width : $break-desktop) {

			}
		}
	}


}

.marketing--media {
	padding : 40px 0 0px 0;
}


.marketing__container {
	max-width : 1192px;
	padding   : 0 16px;

	@media screen and (min-width : $break-table) {
		max-width : 1248px;
		padding   : 0 44px;
	}

	@media screen and (min-width : $break-desktop) {
		max-width    : none;
		padding      : 0;
		padding-left : calc((100% - 1160px) / 2);
	}

	&--reverse {

		@media screen and (min-width : $break-desktop) {
			max-width     : none;
			padding       : 0;
			padding-right : calc((100% - 1160px) / 2);
		}
	}
}

.marketing__wrapper {
	display        : flex;
	flex-direction : column-reverse;

	@media screen and (min-width : $break-table) {

	}

	@media screen and (min-width : $break-desktop) {
		align-items     : center;
		flex-direction  : row;
		justify-content : space-between;
	}

	&--reverse {
		flex-direction : column;
		@media screen and (min-width : $break-table) {

		}

		@media screen and (min-width : $break-desktop) {
			flex-direction : row;
		}
	}
}

.marketing__info {

	@media screen and (min-width : $break-table) {

	}

	@media screen and (min-width : $break-desktop) {
		padding-right : 130px;
	}

	&--reverse {

		@media screen and (min-width : $break-desktop) {
			padding-left  : 130px;
			padding-right : 0;
			width         : auto;
		}

	}
}

.marketing__title {
	color         : #232323;
	font-size     : 24px;
	font-weight   : 700;
	line-height   : 32px;
	margin-bottom : 16px;


	@media screen and (min-width : $break-table) {
		font-size     : 40px;
		line-height   : 48px;
		margin-bottom : 25px;
		max-width     : 586px;
	}

	@media screen and (min-width : $break-desktop) {
		max-width : 448px;
	}
}

.marketing__text {
	color         : #232323;
	font-size     : 14px;
	font-weight   : 500;
	line-height   : 22px;
	margin-bottom : 24px;
	opacity       : 0.5;

	p {
		&:not(:last-child) {
			margin-bottom : 24px;
		}
	}

	@media screen and (min-width : $break-table) {
		font-size     : 16px;
		line-height   : 24px;
		margin-bottom : 32px;
		max-width     : 490px;
	}

	@media screen and (min-width : $break-desktop) {
		max-width : 460px;
	}
}

.marketing__img {
	margin-bottom : 24px;

	@media screen and (min-width : $break-table) {
		margin-bottom : 36px;
	}

	@media screen and (min-width : $break-desktop) {
		height        : 700px;
		margin-bottom : 0;
		width         : calc(100% - 564px);
	}

	@media screen and (min-width : 1642px) {
		width : calc(100% - 558px);
	}

	img {
		height     : 100%;
		object-fit : cover;
		width      : 100%;
	}
}

.marketing__grid {
	display               : grid;
	gap                   : 3px;
	grid-template-columns : repeat(12, 1fr);
	@media screen and (min-width : $break-table) {

	}

	@media screen and (min-width : $break-desktop) {
		gap           : 10px;
		margin-bottom : 0;
		width         : calc(100% - 580px);
	}


	picture {
		display     : block;
		grid-column : 4 span;
	}

	img {
		height     : 100%;
		object-fit : contain;
		width      : 100%;
	}
}

.marketing__wrap {


	@media screen and (min-width : $break-table) {
		align-items     : flex-end;
		display         : flex;
		justify-content : space-between;
	}

	@media screen and (min-width : $break-desktop) {
		align-items    : flex-start;
		flex-direction : column;
	}
}

.marketing__items {
	display        : flex;
	flex-direction : column;
	gap            : 24px;
	margin-bottom  : 24px;

	@media screen and (min-width : $break-table) {
		flex-direction : row;
		gap            : 40px;
		margin-bottom  : 0;
	}

	@media screen and (min-width : $break-desktop) {
		margin-bottom : 48px;
	}
}

.marketing__num {
	color          : $orange;
	font-size      : 24px;
	font-weight    : 500;
	line-height    : 64px;
	padding-bottom : 15px;

	span {
		font-size   : 48px;
		line-height : 64px;

		@media screen and (min-width : $break-table) {

		}

		@media screen and (min-width : $break-desktop) {

		}
	}

	@media screen and (min-width : $break-table) {

	}

	@media screen and (min-width : $break-desktop) {

	}
}

.marketing__about {
	color       : #232323;
	font-size   : 14px;
	font-weight : 500;
	line-height : 24px;
	max-width   : 228px;
	padding-top : 15px;
	position    : relative;

	&:before {
		background-color : rgba(0, 0, 0, 0.2);
		content          : "";
		height           : 1px;
		left             : 0;
		position         : absolute;
		top              : 0;
		width            : 160px;
	}

	@media screen and (min-width : $break-table) {
		max-width   : 180px;
		padding-top : 25px;
	}

	@media screen and (min-width : $break-desktop) {

	}
}

.marketing__buttons {
	display         : flex;
	gap             : 24px;
	justify-content : space-between;

	@media screen and (min-width : $break-table) {
		flex-direction : column;
	}

	@media screen and (min-width : $break-desktop) {
		flex-direction : row;
	}

	.b-btn {
		font-size   : 14px;
		line-height : 22px;
		padding     : 7px 20px;
		width       : 100%;
		@media screen and (min-width : $break-table) {
			font-size   : 16px;
			line-height : 22px;
			min-width   : 169px;
			padding     : 8px 20px 10px 20px;
		}

		@media screen and (min-width : $break-desktop) {

		}
	}
}
